import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import {
  IManagedObject,
  IResultList,
  InventoryBinaryService,
  IOperation,
  OperationService,
  OperationStatus
} from '@c8y/client';
import { AlertService, gettext, ModalService } from '@c8y/ngx-components';
import { assign } from 'lodash-es';
import { switchMap } from 'rxjs/operators';

@Injectable()
export class DiagnosticsService {
  readonly fragment = 'c8y_DiagnosticReport';
  constructor(
    private operationService: OperationService,
    private inventoryBinary: InventoryBinaryService,
    private modalService: ModalService,
    private alertService: AlertService
  ) {}

  isSupportedDevice(device): boolean {
    const supportedOperations = (device && device.c8y_SupportedOperations) || [];
    return supportedOperations.includes(this.fragment);
  }

  getOperations$(device$: Observable<IManagedObject>): Observable<IResultList<IOperation>> {
    return device$.pipe(
      switchMap(device =>
        this.operationService.list({
          deviceId: device.id,
          fragmentType: this.fragment,
          dateFrom: new Date(0).toISOString(),
          dateTo: new Date(Date.now()).toISOString(),
          revert: true,
          pageSize: 10,
          withTotalPages: true
        })
      )
    );
  }

  async createOperation(deviceId: string) {
    const operation = {
      deviceId,
      description: gettext('Diagnostic file request'),
      [this.fragment]: {}
    };
    try {
      await this.operationService.create(operation);
      this.alertService.success(gettext('Diagnostic file request sent.'));
    } catch (error) {
      this.alertService.addServerFailure(error);
    }
  }

  async deleteOperation(operation: IOperation) {
    try {
      const result = await this.modalService.confirm(
        gettext('Delete diagnostic file'),
        gettext('You are about to delete this diagnostic file. Do you want to proceed?'),
        'danger',
        {
          ok: gettext('Delete'),
          cancel: gettext('Cancel')
        }
      );

      if (result) {
        this.deleteDiagnosticsBinary(operation);
      }
    } catch (error) {
      // Do nothing
    }
  }

  async cancelOperation(operation: IOperation) {
    try {
      const operationAfterUpdate = (
        await this.operationService.update({
          id: operation.id,
          status: OperationStatus.FAILED,
          failureReason: gettext('Operation cancelled by user.')
        })
      ).data;
      assign(operation, operationAfterUpdate);
      this.alertService.success(gettext('Diagnostic file request cancelled.'));
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  private getOperation(op: IOperation) {
    if (!op) {
      return null;
    }
    return op && op[this.fragment];
  }

  private async deleteDiagnosticsBinary(op: IOperation) {
    const operation = this.getOperation(op);
    if (operation && operation.file) {
      const { file } = operation;
      try {
        const binaryId = this.inventoryBinary.getIdFromUrl(file);
        const result = await this.inventoryBinary.delete(binaryId);
        if (result) {
          this.deleteDiagnosticsFragment(op);
        }
      } catch (err) {
        if (err.res.status === 404) {
          // In case the file is already deleted via other means we want to delete the fragment
          this.deleteDiagnosticsFragment(op);
        } else {
          const msg = gettext('Could not delete the diagnostic file.');
          this.alertService.danger(msg);
        }
      }
    }
  }

  private async deleteDiagnosticsFragment(op: IOperation) {
    const deleteOp = {
      id: op.id,
      status: op.status,
      [this.fragment]: null
    };
    try {
      const operationAfterUpdate = (await this.operationService.update(deleteOp)).data;
      assign(op, operationAfterUpdate);
      this.alertService.success(gettext('Diagnostic file deleted.'));
    } catch (error) {
      this.alertService.addServerFailure(error);
    }
  }
}
