import { DiagnosticsService } from './diagnostics.service';
import { ActivatedRoute } from '@angular/router';
import { Component } from '@angular/core';
import { BehaviorSubject, pipe } from 'rxjs';
import { map } from 'rxjs/operators';
import {
  ForOfRealtimeOptions,
  operationStatusClasses,
  operationStatusIcons,
  OperationRealtimeService,
  ForOfFilterPipe
} from '@c8y/ngx-components';
import { IManagedObject, IOperation } from '@c8y/client';
@Component({
  templateUrl: './diagnostics.component.html',
  selector: 'c8y-diagnostics'
})
export class DiagnosticsComponent {
  statusIcons = operationStatusIcons;
  statusClasses = operationStatusClasses;
  deviceId: string = this.route.snapshot.parent.data.contextData.id;
  device$ = new BehaviorSubject<IManagedObject>(this.route.snapshot.parent.data.contextData);
  operations$ = this.diagnosticService.getOperations$(this.device$);
  operationsPipe: ForOfFilterPipe<IOperation> = pipe(
    map(ops => ops.filter(op => op[this.diagnosticService.fragment]))
  );
  realtimeOptions: ForOfRealtimeOptions = {
    entityOrId: this.deviceId,
    removeOnUpdate: true,
    insertOnUpdate: true
  } as ForOfRealtimeOptions;
  operationCount: number;

  constructor(
    private route: ActivatedRoute,
    private diagnosticService: DiagnosticsService,
    public operationRealtime: OperationRealtimeService
  ) {}

  onDiagnosticRequest() {
    this.diagnosticService.createOperation(this.deviceId);
  }

  onDelete(operation: IOperation) {
    this.diagnosticService.deleteOperation(operation);
  }

  onCancel(operation: IOperation) {
    this.diagnosticService.cancelOperation(operation);
  }
}
